.h-2 {
	background-color: #eeeae7;
	padding: 0;
	box-sizing: border-box;
	.container-fluid {
		padding: 0; }
	.nav-left {
		padding-top: 160px;
		padding-left: 200px;
		ul {
			padding-left: 0;
			list-style: none;
			li {
				position: relative;
				padding: 22px 0;
				border-bottom: solid 1px rgb(109, 110, 113);
				&:first-child {
					border-top: solid 1px #fff; }
				&::after {
					content: '\e87a';
					font-size: 18px;
					color: rgb(109, 110, 113);
					font-family: Linearicons-Free;
					position: absolute;
					top: 50%;
					left: 0;
					transform: translateY(-50%); }
				a {
					h3 {
						padding-left: 30px;
						font-size: 20px;
						font-family: "Segoe UI";
						color: rgb(109, 110, 113);
						text-transform: uppercase; } } } }
		@media screen and ( max-width:  1440px) {
			padding-left: 0; }

		.btn-new {
			margin-top: 40px; } }
	.nav-container {
		height: 100vh;
		width: 100%;
		.nav-lists {
			list-style-type: none;
			display: flex;
			width: 100%;
			height: 100%;
			padding-left: 0;
			position: relative;

			.nav-list {
				width: 33.33%;
				height: 100%;
				position: relative;
				transition: all 1s ease-in-out;
				cursor: pointer;
				overflow: hidden;
				.list-content {
					position: absolute;
					left: 0;
					bottom: 250px;
					z-index: 999;
					width: 360px;
					height: 140px;
					padding-left: 35px;
					padding-top: 30px;
					overflow: hidden;
					.desc {
						position: relative;
						z-index: 1000;
						h4 {
							font-size: 18px;
							padding-bottom: 30px;
							font-family: "Segoe UI";
							color: rgb(255, 255, 255);
							font-weight: bold;
							text-transform: uppercase;
							text-align: left;
							text-shadow: 0px 2px 5px rgba(0, 0, 0, 0.25); }
						a {
							font-size: 18px;
							font-family: "Segoe UI";
							color: rgb(255, 255, 255);
							text-transform: uppercase;
							text-align: left;
							z-index: 999;
							.lnr {
								font-size: 14px;
								font-family: "Linearicons-Free";
								color: rgb(255, 255, 255);
								text-transform: uppercase;
								padding-left: 10px; } } }
					&::after {
						position: absolute;
						top: 0;
						left: 0;
						transform: translateX(-95%);
						transition: all .5s ease-in-out;
						content: '';
						width: 100%;
						height: 100%;
						background-color: #bfc4c9;
						opacity: .8;
						z-index: 1; } }

				&::after {
					content: '';
					background-image: linear-gradient( 90deg, rgba(0,0,0,0.6) 0%, rgba(0,0,0,0) 50%);
					position: absolute;
					left: 0;
					top: 0;
					width: 100%;
					height: 100%;
					z-index: 170;
					transition: all 1s ease-in-out; }
				img {
					height: 100%;
					width: 100%;
					object-fit: cover; }
				&:hover {
					width: 100%;
					&::after {
						background-image: none; }
					.list-content {
						&::after {
							transform: translateX(0); }
						.desc {
							h4 {
								color: #435c36; }
							a {
								color: #6d6e71;
								.lnr {
									color: #6d6e71; } } } } } } } } }
